/* colorbox */
#colorbox {
  border-top: 50px solid white;
  border-right: 10px solid white;
  border-bottom: 10px solid white;
  border-left: 10px solid white;
  margin-left: -10px;
  z-index: 200000;
}

#colorbox, #cboxOverlay, #cboxWrapper {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 9999;
  outline: none;
  background: white;
  -webkit-transform: translate3d(0, 0, 0);
}

#cboxWrapper {
  max-width: none;
  outline: none;
}

#cboxOverlay {
  position: fixed;
  width: 100%;
  height: 100%;
  background: gray;
  background: rgba(0, 0, 0, 0.6);
}

